:root {
  font: 17px sans-serif;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0 12.5%;
}

main {
  background: radial-gradient(#ddd, #fff);
  border-top: none;
  border-radius: 0 0 18px 18px;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
  display: flex;
}

main > h1 {
  text-shadow: -4px 4px #aaf8;
}

#controls {
  gap: 1rem;
  display: flex;
}

#controls input {
  display: block;
}

#coordinate-submit {
  width: 100%;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #cde;
  border: 1px solid #555;
  border-radius: 12px;
  margin-top: 1rem;
  padding: 1rem;
  font: bold 1rem sans-serif;
  display: block;
}

#coordinate-submit:hover {
  box-shadow: inset 0 0 10px #55c8;
}

#coordinate-submit:active {
  box-shadow: inset 0 0 10px #0008;
}

#query-content {
  text-align: center;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1.8rem;
  padding: 1.1rem;
  display: flex;
}

#city {
  text-transform: uppercase;
  font: bold 1em / 1.5em sans-serif;
}

#city-info {
  text-transform: uppercase;
  color: #0008;
  margin-bottom: 1.4rem;
  font: 1em / 1.5em sans-serif;
}

#forecast {
  font: 2.4rem sans-serif;
}

#temperature {
  font: bold 4.3rem sans-serif;
}

@media (max-width: 430px) {
  :root {
    font-size: 12px;
  }

  #controls {
    flex-direction: column;
  }
}

